
import './mode.scss'

import{history} from '&/history'
import {failToast} from '&/methods'

export class Mode extends Component{


  clickMode=(name)=>{
    if(name =='system' || name == 'add'){
      failToast('暂未开放',1.5)
    }else{
      history.push(name)
    }
    
  }

  render(){
    const {msg,mystyle} = this.props
    const {flexDirection,justifyContent} = mystyle;
    return(
      <div onClick={()=>this.clickMode(msg.name)} style={{backgroundColor:msg.bgcolor,color:msg.fontcolor,width:msg.width,height:msg.height,flexDirection,justifyContent}} className="mode">
        <div><span className={'iconfont '+msg.icon}></span></div>
        <div>{msg.text}</div>
      </div>
    )
  }
}