
import './index.scss'
import Head from '~/components/Head'
import { Drawer, List, NavBar, Icon } from 'antd-mobile';
import { Mode } from '../../components/mode/mode'


const modes = [
  { text: '借书管理', icon: 'icon-jiebei', bgcolor: 'rgba(72, 100, 240, 0.6)', fontcolor: '#fff', width: '90%', height: '100px', name: '/index/mode/bookborrow' },
  { text: '还书管理', icon: 'icon-restore', bgcolor: 'rgba(72, 100, 240, 0.6)', fontcolor: '#fff', width: '90%', height: '100px', name: '/index/mode/bookrepay' },
  { text: '用户管理', icon: 'icon-yonhu', bgcolor: 'rgba(72, 100, 240, 0.6)', fontcolor: '#fff', width: '90%', height: '100px', name: '/index/mode/bookreader' },
]


export default class Borrow extends Component {
  render() {
    return (
      <div className='borrow'>
        <div style={{ marginBottom: '10px' }}>
          <Head title='借还管理' showBack={true}></Head>
        </div>
        <div>
          <div className='options'>
            {
              modes.map((m, i) => {
                return (
                  <Mode mystyle={{ flexDirection: 'row', justifyContent: 'space-around' }} key={i} msg={m}></Mode>
                )

              })
            }
          </div>
        </div>
      </div>
    )
  }
}